.s-gift {
	padding-bottom: 40px;
	overflow: hidden;
	@include media-breakpoint-up(lg) {
		padding-bottom: 44px;
	}
	&__b-section-head {
		@include media-breakpoint-up(xl) {
			margin-bottom: 0;
		}
	}
	&__b-gift {
		width: 780px;
		max-width: 100%;
		@include media-breakpoint-up(md) {
			float: right;

		}
	}
}

