.b-map {
	margin: 30px -20px 0;
	@include media-breakpoint-up(md) {
		display: flex;
		margin-left: 0;
		margin-right: 0;
	}
	@include media-breakpoint-up(lg) {
		margin-top: 40px;
	}
	&__indicators{
		background: $turquoise2;
		color: #FFF;
		padding: 25px 20px;
		@include media-breakpoint-up(md) {
			max-width: percentage(348/1244);
			min-width: 304px;
			padding: 50px 34px;
			margin: 0;
		}
	}
	&__itm {
		&:not(:first-child) {
			margin-top: 5px;
		}
	}
	&__lnk {
		font-weight: bold;
		padding: 5px 10px;
		display: inline-flex;
		align-self: center;
		width: 192px;
		font-size: 18px;
		transition: background-color $time-animation-base, color $time-animation-base;
		border: none;
		color: inherit;
		&:hover {
			background-color: #FFF;
			color: #000;
		}
	}
	&__icon {
		width: 30px;
		height: 20px;
		margin-right: 15px;
		margin-top: 3px;
	}
	&__itm.is-active &__lnk {
		background-color: #FFF;
		color: #000;
	}
	&__map {
		height: 390px;
		@include media-breakpoint-up(md) {
			flex: 1;
			height: 604px;
		}
	}
}
