.s-main-banner {
	position: relative;
	color: #FFF;
	padding-top: 177px;
	padding-left: 20px;
	padding-right: 20px;
	padding-bottom: 40px;
	min-height: 100vh;
	@include media-breakpoint-up(md) {
		padding-top: 121px;
	}
	@include media-breakpoint-up(lg) {
		padding-left: (272/1440*100vw);
		padding-right: (152/1440*100vw);
		padding-top: 121px;

	}
	&_main {
		padding-bottom: 120px;
		@include media-breakpoint-up(lg) {
			padding-top: 213px;
			padding-bottom: 222px;
		}

	}
	&_not-bg {
		background-color: $turquoise2;
		height: auto;
		min-height: 0;
		padding-bottom: 40px;
		@include media-breakpoint-up(lg) {
			padding-bottom: 80px;
		}
	}
	&:before {
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background: linear-gradient(180deg, rgba(0, 0, 0, 0) 57.14%, rgba(0, 0, 0, 0.3) 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45));
		z-index: -1;
	}
	&__video,
	&__bg {
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		position: absolute;
		z-index: -2;
	}
	&__title {
		@include text-style-h1;
		&_large {
			margin:0;
			@include text-style-h1-large;
		}
	}
	&__desc {
		font-size: 18px;
		line-height: 1.4;
		font-family: $font-family-base;
		margin-top: 13px;
		@include media-breakpoint-up(lg) {
			font-size: 36px;
			line-height: 1.3;
		}
	}
	&__info-btm {
		margin-top: 50px;
		@include media-breakpoint-up(md) {
			position: absolute;
			bottom: 0;
			width: 314px;
		}
		&-top {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			@include text-style-middle;
			@include media-breakpoint-up(md) {
				flex-flow: row nowrap;
				justify-content: space-between;
			}
		}
		&-lnk {
			color: inherit;
			margin-top: 20px;
		}
		&-btn {
			margin-top: 30px;
		}
	}

	&__next-section {
		animation: nextDownUp 1.5s infinite ease-in-out alternate;
		@include media-breakpoint-down(lg) {
			@include media-breakpoint-up(lg) {
			    left: 60%;
			}
		}
	}

	&__line {
		display: none;
		align-items: center;
		height: 100%;
		width: 100px;
		position: absolute;
		left: 0;
		top: 0;
		background-color: inherit;
		color: #000;
		z-index: 1;
		@include media-breakpoint-up(lg) {
			display: flex;
			justify-content: center;
		}
		&:after {
			content: '';
			width: 1px;
			position: absolute;
			top: 121px;
			right: 0;
			height: calc(100% - 200px);
			background-color: rgba(#FFF, 0.2);
		}
	}
	&_not-bg &__line {
		height: calc(100vh - 100px);
	}
	&__burger-menu {
		position: static;
		background-color: transparent;

	}
	&__social-group {
		display: none;
		@include media-breakpoint-up(lg) {
			display: flex;
			position: absolute;
			right: (40/1440*100vw);
			bottom: 35px;
		}
	}

	&__info-page {
		margin-top:  10px;
		@include media-breakpoint-up(md) {
			margin-top: 94px;
			display: flex;
		}

	}
	&__page {
		&-ttl {
			letter-spacing: 0.05em;
			@include text-style-middle;
			@include media-breakpoint-up(md) {
				max-width: 197px;
				flex: 0 0 197px;
				padding-top: 0px;
			}
			@include media-breakpoint-up(lg) {
				padding-top: 20px;
			}

		}
		&-desc {
			margin-top: 43px;
			@include media-breakpoint-up(md) {
				margin-top: 0;

			}
		}
		&-title {
			margin: 0;
			max-width: 815px;
		}
		&-txt {
			margin-top: 30px;
			max-width: 460px;
			@include media-breakpoint-up(lg) {
				margin-top: 50px;
			}
		}
	}
	&__btn-download {
		margin-top: 100px;
		max-width: 280px;
		@include media-breakpoint-up(md) {
			max-width: 317px;
			margin-top: 40px;
		}
	}
}
@keyframes nextDownUp {
	0% {
		bottom: 5px;
	}
	50% {
		bottom: 35px;
	}
	100% {
		bottom: 25px;
	}
}
