html {
	font-size:62.5%;
	height: 100%;
}

body {
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-y: scroll;
	color: $font-base-color;
	@include base-text-style;
}
* {
	box-sizing: border-box;
}
.lnk,
a {
	text-decoration: none;
	transition: border-bottom-color $time-animation-base;
	border-bottom: 1px solid;
	color: $font-link-color;
	padding-bottom: 0.3em;
	&_border-brown {
		border-bottom-color: $brown;
		.icon {
			color: $brown;
		}
	}
	&:hover,
	&:focus,
	&:active {
		text-decoration: none;
	}
	&:hover {
		border-bottom-color: transparent;
	}
}
.lnk {
	&__icon {
		&.icon-arrow-right {
			font-size: 14px;
			margin-left: 15px;
		}
	}
}
.container {
	max-width: 1440px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 20px;
	padding-right: 20px;
	@include media-breakpoint-up(lg) {
		box-sizing: content-box;
		padding-left: (156/1440*100vw);
		padding-right: (40/1440*100vw);
	}
}
.s-section {
	padding-top: 40px;
	@include media-breakpoint-up(md) {
		padding-top: 90px;

	}
}
.next-section {
	position: absolute;
	bottom: 30px;
	left: 50%;
	transform: translateX(-50%);
	width: 50px;
	height: 50px;
	background-color: rgba(#FFF, 0.08);
	transition: background-color $time-animation-base;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 100%;
	border: none;
	color: #FFF;
	cursor: pointer;
	@include media-breakpoint-up(md) {
		bottom: 35px;
	}
	&:hover {
		background-color: rgba(#FFF, 0.5);
	}
	&__icon {
			font-size: 14px;
			transform: rotate(90deg);
			transition: transform $time-animation-base;
		}
	&:hover &__icon {
		transform: rotate(90deg) translateX(5px);
	}
}
.burger-menu {
	position: absolute;
	top: auto;
	left: 0;
	background-color: $turquoise2;
	cursor: pointer;
	border:none;
	color: #FFF;
	align-items: center;
	justify-content: center;
	display: flex;
	z-index: 99;
	width: 50px;
	height: 50px;
	@include media-breakpoint-up(lg) {
		width: 100px;
		height: 100px;
	}
	&.is-fixed {
		position: fixed;
		top: 0;
	}
	&:hover {
		background-color: darken($turquoise2, 5%);
	}
	&.is-active &__line {
		background: transparent;
	}
	&.is-active &__line:before {
		top: 0;
		transform: rotate(-45deg);
	}
	&.is-active &__line:after {
		bottom: 0;
		transform: rotate(45deg);
	}

	&__line {
		width: 30px;
		height: 4px;
		background:  currentColor;
		position: relative;
		transition: background-color $time-animation-base;
		&:after, &:before {
			content: '';
			position: absolute;
			left: 0;
			width: 100%;
			height: 100%;
			background:  #FFF;
			transition: top $time-animation-base, bottom $time-animation-base, transform $time-animation-base;
		}
		&:before {
			top: -10px;
		}
		&:after {
			bottom: -10px;
		}
	}
}
.social-group {
	display: flex;
	&__itm {

	}
	&__lnk {
		padding: 0;
		width: 50px;
		height: 50px;
		border: none;
		background-color: rgba(255, 255, 255, 0.08);
		transition: background-color $time-animation-base;
		color: #FFF;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 22px;
		border-radius: 100%;
		&:hover {
			background-color: rgba(255, 255, 255, 0.5);
		}
	}
}
.seporate {
	position: relative;
	border-bottom: 1px solid #000000;
	background-color: #FFF;
	height: 40px;
	margin-top: 40px;
	@include media-breakpoint-up(lg) {
		height: 80px;
		margin-top: 90px;
	}
	&:before {
		background-color: inherit;
		content: '';
		height: 40px;
		border-top: 1px solid #000000;
		border-right: 1px solid #000000;
		position: absolute;
		box-sizing: border-box;
		top: 0;
		left: 0;
		width: 208px;
		@include media-breakpoint-up(lg) {
			height: 80px;
			width: (392/1440*100vw);
		}
	}
	&:after {
		content: '';
		position: absolute;
		width: 207px;
		top: 1px;
		left: 0;
		background-color: #FFF;
		height: 45px;
		@include media-breakpoint-up(lg) {
			height: 85px;
			width: (391/1440*100vw);
		}
	}
	&_mt-0 {
		margin-top: 0;
	}
}
.hint {
	position: absolute;
	background: #FFFFFF;
	z-index: 10;
	padding: 15px;
	font-size: 10px;
	line-height: 1.4;
	border-radius: 10px;
	max-width: 208px;
	left: percentage(346/1440);
	top: percentage(334/800);
	transition: box-shadow $time-animation-base;
	&:not(.is-active) {
		&:hover {
			box-shadow: 0 0 15px rgba(#FFF, 0.5);
		}
	}
	@include media-breakpoint-up(md) {
		padding: 20px;
		max-width: 300px;
		font-size: 14px;
		border-radius: 15px;
	}
	&__close {
		position: absolute;
		top: 0;
		right: 0;
		color: #000000;
		padding: 5px;
		background-color: transparent;
		border: none;
		cursor: pointer;
		@include media-breakpoint-up(lg) {
			padding: 10px;
		}
	}
	&__icon {
		width: 14px;
		height: 14px;
		display: block;
	}
	&__ttl {
		font-weight: bold;
		font-size: 12px;
		@include media-breakpoint-up(md) {
			font-size: 18px;
		}
	}
	&__desc {
		padding-top: 8px;
		@include media-breakpoint-up(md) {
			padding-top: 15px;
		}
	}
	&.is-open {
		margin-top: -5px;
		@include media-breakpoint-up(lg) {
			margin-top: -10px;

		}
	}
	&:not(.is-open) {
		cursor: pointer;
		padding-top: 10px;
		padding-bottom: 10px;
		border-radius: 27px;
		@include media-breakpoint-up(lg) {
			border-radius: 40px;
		}
	}
	&:not(.is-open) &__ttl {
		font-weight: normal;
	}
	&:not(.is-open) &__close,
	&:not(.is-open) &__desc {
		display: none;
	}
}
.breadcrumbs {
	font-size: 12px;
	line-height: 1.4;
	color: #FFF;
	&__list {
		margin: 0;
		padding: 0;
		list-style: none;
	}
	&__itm {
		display: inline-block;
		vertical-align: top;
		&:before {
			content: '•' ;
			display: inline-block;
			vertical-align: top;
			margin: 0 5px;
		}
		&_first {
			&:before {
				display: none;
			}
		}
	}
	&__lnk {
		color: inherit;
	}
}
p {
	margin: 15px 0;
	@include media-breakpoint-up(lg) {
		margin: 25px 0;
	}
}
.video-box {
	display: block;
	border: none;
	position: relative;
	&__img {
		display: block;
		width: 100%;
	}
	&:hover &__play {
		color: $brown;
		font-size: 50px;
		border-width: 10px;
	}
	&__play {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 70px;
		height: 70px;
		border-radius: 100%;
		color: #FFF;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid currentColor;
		font-size: 10px;
		transition: font-size $time-animation-base, color $time-animation-base, border-width $time-animation-base;
		@include media-breakpoint-up(md) {
			font-size: 18px;
			width: 170px;
			height: 170px;
		}

	}
}


.icon {
	&-leisure ,
	&-leisure ,
	&-medicine,
	&-basket,
	&-education {
		stroke-width: 2;
		fill: none;
		stroke: currentColor;
	}
	&-leisure {
		stroke-width: 2;
		stroke-linejoin: round;
		stroke-linecap: round;
	}
}
.list-marker,
.list-number {
	&__itm {
		counter-increment: list;
		position: relative;
		padding-left: 60px;
		min-height: 40px;
		&:not(:first-child) {
			margin-top: 30px;
			@include media-breakpoint-up(lg) {
				margin-top: 50px;
			}
		}
		@include media-breakpoint-up(lg) {
			padding-left: 116px;
			min-height: 80px;
		}
		&:before {
			position: absolute;
			left: 0 ;
			top: 0;
			content: counter(list);
			flex: 0 0 40px;
			font-size: 24px;
			color: #FFF;
			background-color: $turquoise2;
			width: 40px;
			height: 40px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-weight: bold;
			@include media-breakpoint-up(lg) {
				width: 80px;
				height: 80px;
				font-size: 30px;

			}
		}
	}
	&__ttl {
		font-weight: bold;
	}
	&__links {
		margin-top: 17px;
		@include media-breakpoint-up(lg) {
			margin-top: 30px;
		}
	}
	&__itm-link {
		margin-top: 10px;
	}
	&__lnk {
		@include media-breakpoint-up(lg) {
			margin-right: 30px;
		}
	}
	.footnote {
		margin-top: 10px;
		margin-bottom: 20px;
		display: block;
		@include media-breakpoint-up(lg) {
			display: inline;
			margin-top: 0;
			margin-bottom: 0;

		}
	}

}
.list-marker {
	&__itm {
		padding-left: 25px;
		&:before {
			content: '';
			width: 10px;
			height: 10px;
			top: (5/16*1em);
		}
	}
}
.footnote {
	@include text-style-small;
}
.pagination {
	margin-top: 40px;
	&__list {
		list-style: none;
		margin:0;
		padding: 0;
		display: flex;
	}
	&__itm {
		flex: 0 0 50px;
	}
	&__lnk {
		background: #FFFFFF;
		border: 1px solid $gray4;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 50px;
		font-size: 16px;
		height: 50px;
		color: #000;
		border-radius: 100%;
		transition: border-color $time-animation-base, background-color $time-animation-base, color $time-animation-base;
		&.is-active,
		&:hover {
			background-color: $turquoise2;
			border-color: $turquoise2;
			color: #FFF;
		}
	}
}
.row {
	@include media-breakpoint-up(md) {
		display: flex;
		gap: 30px;
	}
	&__col {
		padding-top: 20px;
		@include media-breakpoint-up(lg) {
			padding-top: 0;

		}
		&_1-2 {
			@include media-breakpoint-up(lg) {
				flex: 0 0 calc(50% - 30px);
				max-width: calc(50% - 30px);
			}
		}
		&_1-2 {
			@include media-breakpoint-up(lg) {
				flex: 0 0 calc(50% - 30px);
				max-width: calc(50% - 30px);
			}
		}
		&_1-3 {
			@include media-breakpoint-up(lg) {
				flex: 0 0 calc(33.333% - 30px);
				max-width: calc(33.333% - 30px);
			}
		}
		&_big {
			@include media-breakpoint-up(lg) {
				flex: 1;
				padding-right: (146/1244*100vw);
			}
		}
		&_small{
			@include media-breakpoint-up(lg) {
				flex: 0 0 317px;
				max-width: 317px;
			}
		}
	}
	&_gap-22 {
		@include media-breakpoint-up(lg) {
			gap: 22px;
		}
	}
	&_gap-22 &__col_1-2 {
		@include media-breakpoint-up(lg) {
			flex: 0 0 calc(50% - 22px);
			max-width: calc(50% - 22px);
		}
	}
	&_gap-22 &__col_1-2 {
		@include media-breakpoint-up(lg) {
			flex: 0 0 calc(50% - 22px);
			max-width: calc(50% - 22px);
		}
	}
	&_gap-22 &__col_1-3 {
		@include media-breakpoint-up(lg) {
			flex: 0 0 calc(33.333% - 22px);
			max-width: calc(33.333% - 22px);
		}
	}

}


.table-wrap {
	overflow-x: auto;
	overflow-y: hidden;
	webkit-overflow-scrolling: touch;
}
table {
	width: 100%;
	border-collapse: collapse;
	min-width: 500px;
	th {
		font-weight: bold;
		color: #FFF;
		background-color: $turquoise2;
		padding: 10px;
		text-align: left;
		@include media-breakpoint-up(lg) {
			padding: 26px 20px;
		}
	}
	tr {
		&:first-child {
			td {
				border-top: none;
			}
		}
	}
	td {
		padding: 10px;
		border-top: 1px solid $gray5;
		font-size: 16px;
		@include media-breakpoint-up(lg) {
			padding: 20px;
		}
	}
}
.lnk-download {
	display: inline-block;
	position: relative;
	padding-left: 70px;
	border: none;
	min-height: 70px;
	color: inherit;
	&__icon {
		color: $brown;
		top: 0;
		left: 0;
		position: absolute;
		font-size: 72px;
	}
	&__icon {
		font-size: 70px;

	}
	&:hover &__ttl {
		border-bottom-color: transparent;
	}
	&__ttl {
		border-bottom: 1px solid ;
		line-height: 1.4;
		padding-bottom: 0.2em;
		transition: border-color $time-animation-base;
	}
	&__footnote {
		color: $gray6;
		margin-top: 5px;
	}
}
img {
	max-width: 100%;
	display: block;
}
blockquote {
	position: relative;
	padding: 0 0 0 49px;
	background-image: url("../images/q.svg");
	background-repeat: no-repeat;
	background-position: 0 5px;
	margin: 15px 0;
	@include media-breakpoint-up(lg) {
		margin: 25px 0;
	}
}
.pict-desc {
	&__img {
		display: block;
		width: 100%;
	}
	&__desc {
		padding: 20px 0 0;
		@include media-breakpoint-up(lg) {
			padding: 40px 20px 0;
		}
		&_ps {
			padding-top: 10px;
		}
		*:first-child {
			margin-top: 0;
		}
		*:last-child {
			margin-bottom: 0;
		}
	}
}
