.s-flat {
	position: relative;
	overflow: hidden;
	padding-bottom: 190px;
	@include media-breakpoint-up(md) {
		padding-bottom: 164px;
	}
	&__bg {
		position: absolute;
		bottom: 0;
		right: 0;
		max-width: 100%;
		@include media-breakpoint-up(md) {
			right: 40px;
			max-width: percentage(800/1244);
		}
	}
	&__info {
	}
	&__row {
		@include media-breakpoint-up(md) {
			display: flex;
			margin-top: 50px;
		}
		&_two {
			@include media-breakpoint-up(md) {
				margin-top: 40px;

			}
		}
	}
	&__col {
		margin-top: 40px;
		@include media-breakpoint-up(md) {
			margin-top: 0;

		}
		&_first {
			@include media-breakpoint-up(md) {
				max-width: 463px;
				flex: 0 0 463px;
				padding-right: 53px;
			}
		}
	}
	&__ttl {
		font-weight: bold;
		margin-bottom: 20px;
	}
	&__list {
		@include media-breakpoint-up(md) {
			display: flex;
			flex-flow: row wrap;
			justify-content: space-between;
		}
	}
	&__itm {
		margin-bottom: 20px;
		@include media-breakpoint-up(md) {
			max-width: 178px;
			flex: 0 0 178px;
			margin-bottom: 30px;

		}
	}
	&__par {
		font-weight: bold;
	}
}
