.b-thanks {
	max-width: 949px;
	padding: 25px 15px;
	display: none;
	@include media-breakpoint-up(md) {
		padding: 100px 75px;
	}
	&.fancybox__content > button.f-button.is-close-btn{
		display: none;
	}
	&__close {
		top: 0;
		right: 0;
		border-radius: 0;
		width: 50px;
		height: 50px;
		background-color: transparent;
		padding: 0;
		border: none;
		color: $brown;
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		opacity: 1;
		svg {
			width: 25px;
			height: 25px;
		}
	}
	&__box {
		position: relative;
		padding-left: 80px;
		@include media-breakpoint-up(lg) {
			padding-left: 160px;
		}
	}
	&__icon-wrap {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 60px;
		height: 60px;
		background-color: $brown;
		border-radius: 100%;
		position: absolute;
		font-size: 26px;
		top: 0;
		left: 0;
		color: #FFF;
		@include media-breakpoint-up(lg) {
			width: 120px;
			height: 120px;
		}
	}
	&__txt {
		margin-top: 20px;
	}
}
