.s-header {
	flex: 0 0 auto;
	padding: 4.5px 20px;
	position: absolute;
	top:0;
	left: 0;
	width: 100%;
	z-index: 90;
	color: #FFF;
	font-size: 14px;
	line-height: 1.55;
	@include media-breakpoint-up(md) {
		padding-right: 0;
		padding-top: 0;
		padding-bottom: 0;
	}
	@include media-breakpoint-up(lg) {
		padding: 0 0 0 (40/1440*100vw);
	}
	&__container {
		display: flex;
		flex-flow: row wrap;
		@include media-breakpoint-up(md) {
			flex-flow: nowrap;
			align-items: center;
		}

	}
	&__burger-menu {
		margin-left: -10px;
		margin-top: 15px;
		width: 50px;
		height: 50px;
		position: static;
		background-color: transparent;
		display: flex;
		@include media-breakpoint-up(md) {
			margin-bottom: -5px;
		}
		&:hover {
			background-color: transparent;
		}
		@include media-breakpoint-up(lg) {
			margin-bottom: 0;
			display: none;
		}

	}
	&__logo {
		border: none;
		padding-bottom: 0;
		margin-left: ( 36 / 320 * 100vw);
		@include media-breakpoint-up(md) {
			margin-left: 0;
		}
		&-img {
			display: block;
			width: 145px;
			height: 60px;
			@include media-breakpoint-up(md) {
				width: 140px;
				height: 55px;
			}
			@include media-breakpoint-up(lg) {
				width: calc(169/1440*100vw);
				height: calc(71/1440*100vw);
					max-height: 80px;
			}
		}
	}

	&__address {
		display: none;
		@include media-breakpoint-up(lg) {
			display: block;
			padding-right: 15px;
				margin-left: (63.15/1440*100vw);
				flex: 0 0 (350/1440*100vw);
				max-width: (350/1440*100vw);
		}
	}

	&__info {
		display: none;
		@include media-breakpoint-up(lg) {
			display: block;
		}
	}

	&__phone {
		font-size: 0;
		color: inherit;
		margin-left: auto;
		border: none;
		padding-bottom: 0;
		margin-top: 23px;
		@include media-breakpoint-up(lg) {
			margin-top: 0;
			margin-right: 17px;
		}
		@include media-breakpoint-down(md) {
			@include media-breakpoint-up(md) {
				font-size: 16px;
				margin-top: 0;
				margin-right: 17px;
			}
		}
		@include media-breakpoint-up(xl) {
			font-size: 16px;
		}
		&-icon {
			display: block;
			font-size: 25px;
			@include media-breakpoint-down(md) {
				@include media-breakpoint-up(md) {
					display: none;
				}
			}
			@include media-breakpoint-up(xl) {
				display: none;
			}
		}
	}

	&__btn {
		margin-top: 12px;
		@include media-breakpoint-up(md) {
			margin-top: 0;
		}
	}
}
