.s-slider-desc {
	overflow: hidden;
	&__row {
		@include media-breakpoint-up(md) {
			display: flex;
		}
	}
	&__col {
		&_slider {
			@include media-breakpoint-up(md) {
				max-width: percentage(665/1244);
				flex: 0 0 percentage(665/1244);

			}
		}
		&_desc {
			position: relative;
			z-index: 1;
			@include media-breakpoint-up(md) {
				flex: 1;
				padding-left: (116/1440*100vw);
			}
		}
	}
	&__swiper {
		overflow: visible;
		margin-top: 20px;
		z-index: 1;
		&:after {
			content: '';
			height: 120%;
			width: 100vw;
			left: 100%;
			top: -10%;
			position: absolute;
			background-color: #FFF;
			z-index: 5;
		}
		@include media-breakpoint-up(md) {
			margin-top: 0;

		}
		&-button-prev {
			position: absolute;
			top: 10px;
			right: 40px;
			@include media-breakpoint-up(md) {
				top: 30px;
				right: 80px;
			}
		}
		&-button-next {
			position: absolute;
			top: 10px;
			right: 10px;
			@include media-breakpoint-up(md) {
				top: 30px;
				right: 30px;
			}
		}
		&-slide {
			display: flex;

			@include media-breakpoint-up(lg) {

			}
		}
		&-slide.swiper-slide-active &-picture {
			width: 100%;
			transition: width $time-animation-base;
		}

		&-slide.swiper-slide-prev &-picture,
		&-slide.swiper-slide-next &-picture {
			width: percentage(495/665);
			transition: width  $time-animation-base;
			@include media-breakpoint-up(lg) {

			}
		}
		&-slide.swiper-slide-prev {
			align-items: flex-end;
			justify-content: flex-end;
			padding-right: 10px;
			@include media-breakpoint-up(lg) {
				padding-right: (68/1440*100vw);
			}
		}
		&-slide.swiper-slide-next {
			align-items: flex-end;
			justify-content: flex-start;
			padding-left: 10px;
			@include media-breakpoint-up(lg) {
				padding-left: (68/1440*100vw);
			}
		}

		&-picture {
			display: block;
			width: 100%;
		}
		&-img {
			width: 100%;
			display: block;

		}
		&-pagination {
			position: absolute;
			z-index: 10;
			bottom: 10px;
			right: 5px;
			@include media-breakpoint-up(lg ) {
				bottom: 30px;
				right: 25px;
			}
		}
	}

	&__title {
		margin: 0;
		&_visible-mobile {
			@include media-breakpoint-up(md) {
				display: none;

			}
		}
		&_hide-mobile {
			display: none;
			@include media-breakpoint-up(md) {
				display: block;
			}
		}
	}

	&__txt {
		margin-top: 30px;
	}

}
