// Font
$font-family-base: 'PP Pangram Sans', Arial, Helvetica, sans-serif;
$font-family-head: 'Gotham Pro', Arial, Helvetica, sans-serif;

/**
 * Grid system
 * -----------------------------------------
 */

//Breakpoints
$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1400px
);

// Colors
$black: #000;
$turquoise: #1A4A58;
$turquoise2: #194552;
$brown: #B98347;
$gray: #E2E2E2;
$gray2: #EFEFEF;;
$gray3: #989898;
$gray4: #D9D9D9;
$gray5: #A6A6A6;
$gray6: #999999;
$blue-light: #4DBDDE;
$red: #FF0000;

$font-base-color: $black;
$font-link-color: $turquoise2;



// time
$time-animation-base: 0.3s;

//buttons
$font-button-base: $font-family-base;
