.b-layout {
	border: none;
	display: block;
	&__pict {
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #000;
		position: relative;
		height: 229px;

		@include media-breakpoint-up(md) {
			height: auto;
			padding-bottom: percentage(229/260);
		}
	}
	&_big &__pict {
		@include media-breakpoint-up(md) {
			padding-bottom: percentage(585/685);
		}

	}
	img {
		max-width: 90%;
		max-height: 90%;
		@include media-breakpoint-up(md) {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
		}
	}
	&__lnk {
		display: inline-flex;
		vertical-align: top;
		margin-top: 10px;
		font-size: 18px;
		line-height: 1.4;
		font-family: $font-family-base;
		@include media-breakpoint-up(md) {
			margin-top: 20px;
			max-height: 75px;
			overflow: hidden;
		}
		@include media-breakpoint-down(md) {
			@include media-breakpoint-up(sm) {
			    font-size: 15px;
			}

		}
		@include media-breakpoint-up(xl) {
			font-size: 24px;
		}
		&-icon {
			color: $brown;
			margin-left: 20px;
			margin-top: 12px;
			flex: 0 0 20px;
			height: 14px;
		}
	}
}
