.s-plan {
	&__plan {
		margin-left: -20px;
		margin-right: -20px;
		@include media-breakpoint-up(md) {
			margin: 0;
		}
	}
	&__img {
		display: block;
		height: 381px;
		max-width: 100%;
		object-fit: cover;
		@include media-breakpoint-up(md) {
			object-fit: none;
			height: auto;
			max-width: 100%;
		}
	}
}

