.s-infrastructure {
	position: relative;

	@include media-breakpoint-up(md) {
		height: auto;
	}

	&__box {
		width: 100%;
		overflow-x: auto;
		overflow-y: hidden;
		-webkit-overflow-scrolling: touch;
		position: relative;

	}
	&__picture {
		position: relative;
		&:after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2))
		}
	}
	&__pict {
		display: block;
		width: 100%;
		min-width: 790px;
		position: relative;
		&:after,
		&:before {
			z-index: 5;
			content: '';
			position: absolute;
			background-color: #FFF;
			height: 60px;
			width: 145px;
			@include media-breakpoint-up(lg) {
				height: 110px;
				width: (855/1140*100vw);
			}
		}
		&:before {
			top: -10px;
			right: 0;
		}
		&:after {
			bottom: -10px;
			left: 0;
		}
	}
	&__bg {
		display: block;
		height: 444px;
		object-fit: cover;
		@include media-breakpoint-up(md) {
			width: 100%;
			height: auto;
		}
	}
}
