.s-bg-image {
	position: relative;
	color: #FFF;
	&__picture {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(270deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 49.03%), linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4))
		}
	}
	&__bg {
		height: 702px;
		object-fit: cover;
		display: block;
		width: 100%;
		@include media-breakpoint-up(md) {
			height: 850px;
		}
	}
	&__box {
		min-height: 702px;
		position: relative;
		display: flex;
		@include media-breakpoint-up(md) {
			min-height: 850px;
		}
	}
	&__container {
		position: relative;
		z-index: 10;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 100%;
	}
	&__top {
		@include media-breakpoint-up(md) {
			max-width: percentage(350/1244);
		}
	}
	&__btm {
		@include media-breakpoint-up(md) {
			margin-top: auto;
			margin-left: auto;
			padding-top: 40px;
			max-width: percentage(637/1244);
		}
		@include media-breakpoint-up(xl) {
			max-width: percentage(437/1244);
		}
		&_has-padding {
			padding-bottom: 30px;
			@include media-breakpoint-up(lg) {
				padding-bottom: 90px;
			}
		}
	}
	&__txt {
		*:last-child {
			margin-bottom: 0;
		}
		&_btm {
			@include media-breakpoint-up(md) {
				margin-right: (120/1440*100vw);
			}
		}
	}
	&__title {
		margin-bottom: 30px;
		@include media-breakpoint-up(lg) {
			margin-bottom: 40px;
		}
	}
	&__btn-wrap {
		margin: 30px -20px 0;
		@include media-breakpoint-up(lg) {
			margin: 40px (-40/1440*100vw) 0 0;
		}
	}
	&__btn {
		@include media-breakpoint-up(md) {
			justify-content: flex-start;
			padding-left: 40px;

		}
	}
}
