.s-news {
	@include media-breakpoint-up(lg) {
		padding-top: 80px;
	}
	&__pagination {
		@include media-breakpoint-up(lg) {
			margin-left: (272px - 156px);
		}
	}
	&__list {
		@include media-breakpoint-up(lg) {
			margin-left: (272px - 156px);
		}
	}
	&__itm {
		padding-bottom: 30px;
		display: flex;
		border-bottom: 1px solid $gray4;
		&:not(:first-child) {
			padding-top: 30px;
		}
		@include media-breakpoint-up(lg) {
			padding-bottom: 40px;
			&:not(:first-child) {
				padding-top: 40px;
			}
		}
	}
	&__date {
		color: $brown;
		max-width: 71px;
		flex: 0 0 71px;
		margin-right: 19px;
		@include media-breakpoint-up(lg) {
			max-width: 80px;
			flex: 0 0 80px;
			margin-right: 121px;
		}
	}
	&__mont,
	&__year {
		font-weight: normal;
	}
	&__desc {
		@include text-style-large;
	}
	&__ttl {
		color: inherit;
		font-weight: bold;
	}
}
