.s-contacts {
	@include media-breakpoint-up(lg) {
		padding-top: 80px;
	}
	&__container {
		@include media-breakpoint-up(lg) {
			display: flex;
		}
	}
	&__desc {
		@include media-breakpoint-up(lg) {
			max-width: 347px;
			flex: 0 0 347px;
			padding-right: 30px;
		}
	}
	&__map {
		height: 156px;
		margin-top: 30px;
		@include media-breakpoint-up(lg) {
			margin-top: 0;
			height: 500px;
			flex: 0 0 calc(100% - 347px);
			max-width: calc(100% - 347px);
		}
	}
	&__title {
		margin: 0;
	}
	&__mail,
	&__phone {
		border-color: transparent;
		&:hover {
			border-color: currentColor;
		}
	}
	&__contact,
	&__address {
		margin-top: 30px;
		@include media-breakpoint-up(lg) {
			margin-top: 40px;

		}
	}
}
