.s-article {
	@include media-breakpoint-up(lg) {
		padding-top: 80px;
		margin-left: (272px - 156px);
	}
	&__article {
		@include media-breakpoint-up(lg) {
			display: flex;
		}
	}
	&__date {
		color: $brown;
		max-width: 71px;
		flex: 0 0 71px;
		margin-right: 19px;
		@include media-breakpoint-up(lg) {
			max-width: 80px;
			flex: 0 0 80px;
			margin-right: 151px;
		}
	}
	&__title {
		margin-top: 0;
		margin-bottom: 20px;
		font-weight: bold;
		@include media-breakpoint-up(lg) {
			font-size: 30px;
			margin-bottom: 40px;
		}
	}
	&__desc {
		margin-top: 20px;
		max-width: 781px;
		flex: 1;
		@include media-breakpoint-up(lg) {
			margin-top: 0;
		}
	}
	&__gallery {
		display: flex;
		flex-flow: row wrap;
		gap: 10px;
		@include media-breakpoint-up(lg) {
			gap: 35px;
		}
	}
	&__img {
		display: block;
		max-width: 100%;
	}
}
