.b-faq {
	&__itm {
		&:not(:first-child){
			margin-top: 10px;
		}
	}
	&__box {
		border: 1px solid ;
		transition: border $time-animation-base, background-color $time-animation-base;
	}
	&__box.is-active {
		background: $gray2;
		border-color: $gray2;
	}
	&__ttl {
		cursor: pointer;
		position: relative;
		font-weight: bold;
		padding: 10px 40px 10px 10px;
		@include media-breakpoint-up(lg) {
			padding: 26px 80px 26px 20px;
		}
	}
	&__desc {
		display: none;
		padding: 4px 40px 10px 10px;
		@include media-breakpoint-up(lg) {
			padding: 4px 80px 26px 20px;
		}
	}
	&__icon-wrap {
		position: absolute;
		right: 10px;
		top: 15px;
		width: 30px;
		height: 30px;
		border-radius: 100%;
		background-color: $brown;
		color: #FFF;
		display: flex;
		align-items: center;
		justify-content: center;
		@include media-breakpoint-up(lg) {
			top: 20px;
			right: 20px;
			width: 40px;
			height: 40px;
		}
	}
	&__icon {
		font-size: 10px;
		margin-top: 5px;
		transition: transform $time-animation-base, margin $time-animation-base;
	}
	&__box.is-active &__icon {
		transform: rotate(180deg);
		margin: 0;
	}
}
