@mixin base-text-style () {
	font-size: 16px;
	line-height: 1.4;
	font-weight: 500;
	font-style: normal;
	font-family: $font-family-base;
	@include media-breakpoint-up(lg) {
		font-size: 18px;
	}
}
@mixin text-style-h1-large() {
	font-size: 42px;
	line-height: 1.2;
	font-weight: bold;
	font-family: $font-family-head;
	@include media-breakpoint-up(md) {
		font-size: 90px;
	}
	@include media-breakpoint-up(xl) {
		font-size: 130px;
	}
}
@mixin text-style-h1() {
	font-size: 24px;
	line-height: 1;
	font-weight: 700;
	font-family: $font-family-base;
	@include media-breakpoint-up(lg) {
		font-size: 50px;
	}
}
@mixin text-style-h2() {
	font-size: 24px;
	font-weight: 700;
	line-height: 1.4;
	font-family: $font-family-base;
	@include media-breakpoint-up(lg) {
		font-size: 36px;
	}
}
@mixin text-style-h3() {
	font-size: 24px;
	font-weight: 700;
	line-height: 1.4;
	font-family: $font-family-base;
	@include media-breakpoint-up(lg) {
		font-size: 30px;
	}
}
@mixin text-style-h4() {
	font-size: 18px;
	line-height: 1.4;
	font-family: $font-family-base;
	@include media-breakpoint-up(lg) {
		font-size: 24px;
	}
}
@mixin text-style-large () {
	font-size: 18px;
	font-weight: 500;
	line-height: 1.4;
	font-family: $font-family-base;
	@include media-breakpoint-up(lg) {
		font-size: 30px;
	}
}
@mixin text-style-middle () {
	font-size: 16px;
	font-weight: 500;
	line-height: 1.4;
	font-family: $font-family-base;
}

@mixin text-style-small () {
	font-size: 12px;
	font-weight: 500;
	line-height: 1.4;
	font-family: $font-family-base;
}

h1,.h1 {
	@include text-style-h1;
}
h2, .h2 {
	@include text-style-h2;
}
h3, .h3 {
	@include text-style-h3;
}


.txt {
	&_brown {
		color: $brown;
	}
	&_ib {
		display: inline-block;
		vertical-align: top;
	}
}
