.form {
	&__form-group {
		margin-bottom: 10px;
	}
	&__control {
		font-size: 18px;
		line-height: 1.4px;
		border: 1px solid $gray2;
		background-color: $gray2;
		outline: none;
		box-shadow: none;
		display: block;
		width: 100%;
		padding: 21px 20px;
		height: 60px;
		color: #000;
		transition: border-bottom-color $time-animation-base, color $time-animation-base;
		@include media-breakpoint-up(lg) {
			padding: 26px 20px;
			height: 70px;
		}
		&::placeholder {
			color: $gray3;
			opacity: 1;
		}
	}

	&__control.is-invalid:invalid + &_error {
		display: block;
	}
	&__control.is-invalid:invalid {
			border-color: $red;
	}
	&__error {
		display: none;
		color: $red;
		padding-top: 10px;
		@include text-style-middle;
	}
	&__capcha {
		margin-top: 10px;
		max-width: 326px;
		img {
			display: block;
			width: 100%;
		}
	}
	&__footnote {
		margin-top: 10px;
		@include text-style-small;
	}
	&__btn {
		margin-top: 20px;
	}
}
