.s-desc-pict {
	padding-top: 30px;
	@include media-breakpoint-up(lg) {
		padding-top: 0
	}
	&__row {
		@include media-breakpoint-up(md) {
			display: flex;
			max-width: 1012px;
			margin: 0 auto;
		}
	}
	&__col {
		&_desc {
			@include text-style-middle;
			@include media-breakpoint-up(md) {
				align-self: center;
				max-width: percentage(317/1012);
				flex: 0 0 percentage(317/1012);
				padding-right: 44px;
			}
		}
		&_pict {
			padding-top: 30px;
			@include media-breakpoint-up(md) {
				flex: 1;
				padding-top: 0;
			}
		}
	}
}
