.b-standarts {
	margin-top: 40px;
	position: relative;
	@include media-breakpoint-up (lg) {
		margin: 0px;
	}
	&_put {
		@include media-breakpoint-up (lg) {
			margin-top: -110px;
			z-index: -2;
		}
		@include media-breakpoint-up(xxl) {
			margin-top: (-110/1440*100vw);
			z-index: -2;
		}
		@include media-breakpoint-up(xxl) {
			margin-top: (-180/1440*100vw);
		}
	}
	&__container {
		@include media-breakpoint-up (lg) {
			min-height: 540px;
			padding-top: 214px;
			padding-bottom: 46px;
		}
		@include media-breakpoint-up(xxl) {
			padding-top: 314px;
			min-height: (440/1440*100vw);
		}

	}
	&_put &__container {

	}
	&__img {
		display: block;
		width: 100%;
		@include media-breakpoint-up (lg) {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			z-index: -1;
		}
	}
	&__ttl {
		font-weight: normal;
		margin: 10px 0 0;
		@include media-breakpoint-up (lg) {
			display: inline-block;
			padding: 10px 20px;
			background-color: #FFF;

		}
	}
	&__list {
		margin-top: 10px;
		@include media-breakpoint-up (lg) {
			display: grid;
			grid-template-columns: repeat(4, 1fr);
			grid-gap: 11px;
		}
	}
	&__itm {
		&:not(:first-child) {
			margin-top: 20px;
		}
		@include media-breakpoint-up (lg) {
			&:not(:first-child) {
				margin-top: 0px;
			}
			padding: 30px;
			background-color: #FFF;
		}
		&_last {
			grid-column: 4 / 8;
			grid-row: 1/3;
		}
	}
}
