.b-gift {
	padding: 30px 20px 0;
	overflow: hidden;
	position: relative;
	background-color: $turquoise2;
	color: #FFF;
	display: flex;
	flex-direction: column;
	@include media-breakpoint-up(md) {
		padding: 50px 50px 0;
	}
	&__title {
		margin: 0;
		@include text-style-h2;
		@include media-breakpoint-up(md) {
			max-width: 370px;
		}
	}
	&__desc {
		margin-top: 20px;
		@include media-breakpoint-up(md) {
			margin-top: 30px;
			max-width: 263px;
		}
	}
	&__picture {
		position: absolute;
		left: 20px;
		bottom: -70px;

		@include media-breakpoint-up(md) {
			bottom: auto;
			top: 50px;
			left: 420px;
		}
	}
	&__img {
		width: 299px;
		height: 299px;
		border-radius: 100%;
		object-fit: cover;
		@include media-breakpoint-up(md) {
			width: 412px;
			height: 412px;
		}
	}
	&__btn {
		margin-left: -20px;
		font-weight: normal;
		max-width: 240px;
		margin-top: 189px;
		position: relative;
		@include media-breakpoint-up(md) {
			max-width: 317px;
			margin-top: 84px;
			margin-left: -50px;
		}
	}
}
