.s-main-menu {
	background-color: $turquoise2;
	padding: 25px 20px 60px;
	top: 0;
	left: 0;
	width: 100%;
	color: #FFF;
	z-index: 101;
	max-width: 300px;
	transition: visibility $time-animation-base, opacity $time-animation-base;
	position: fixed;
	overflow-x: hidden;
	overflow-y: auto;
	height: 100%;
	&:not(.is-open) {
		visibility: hidden;
		opacity: 0;
	}
	@include media-breakpoint-up(lg) {
		max-width: (997/1440*100vw);
		padding-top: 0;
		padding-left: 0;
		display: flex;
		flex-flow: row wrap;
	}
	@include media-breakpoint-up(xxl) {
	    max-width: (997/1440*100vw);
	}
	&__top {
		display: flex;
		justify-content: space-between;
		flex-flow: row wrap;
		min-width: 100%;
		@include media-breakpoint-up(lg) {
			height: 80px;
			flex-flow: nowrap;
			justify-content: flex-start;
			align-items: center;
			padding: 0 0 0 (40/1440*100vw);
		}
	}
	&__info, &__address {
		margin-top: 20px;
		font-size: 12px;
		line-height: 1.55;
		@include media-breakpoint-up(lg) {
			font-size: 14px;
			margin-top: 0;
		}
	}
	&__logo {
		border: none;
		padding-bottom: 0;
		display: none;
		@include media-breakpoint-up(lg) {
			display: block;
		}
		&-img {
			display: block;
			@include media-breakpoint-up(lg) {
				width: calc(169/1440*100vw);
				height: calc(71/1440*100vw);
				max-height: 80px;
			}
		}
	}
	&__address {
		@include media-breakpoint-up(lg) {
			padding-right: 15px;
			margin-left: (63.15/1440*100vw);
			flex: 0 0 (350/1440*100vw);
			max-width: (350/1440*100vw);
		}
	}
	&__info {
		@include media-breakpoint-up(lg) {
			//margin-left: (199/1440*100vw);
		}
	}
	&__phone {
		font-size: 24px;
		line-height: 1.55;
		color: inherit;
		padding: 0;
		font-weight: bold;
		border: none;
		@include media-breakpoint-up(lg) {
			display: none;

		}
	}
	&__btn {
		margin-top: 15px;
		@include media-breakpoint-up(lg) {
			display: none;

		}
	}

	&__close {
		font-size: 25px;
		@include media-breakpoint-up(lg) {
			display: none;
		}
	}
	&__nav {
		font-size: 20px;
		line-height: 1.55;
		margin-top: 40px;
		@include media-breakpoint-up(lg) {
			font-size: 25px;
			margin-top: 0;
			padding-left: (272/1440*100vw);
			flex: 0 0 (622/1440*100vw);
			max-width: 0 0 (622/1440*100vw);
		}
		@include media-breakpoint-up(xl) {
			font-size: 30px;
		}
		@media (max-height: 800px) and (min-width: 992px) {
			font-size: 20px;
		}
		&-list {
			margin: 0;
			padding: 0;
			list-style: none;
		}
		&-itm {
			&:not(:first-child) {
				margin-top: 20px;
				@include media-breakpoint-up(lg) {
					margin-top: 25px;
				}
				@media (max-height: 800px) and (min-width: 992px) {
					margin-top: 20px;
				}
			}
		}
		&-itm.is-active &-lnk {
			border-bottom-color: $brown;
		}

		&-lnk{
			color: inherit;
			border-bottom-color: transparent;
			&:hover {
				border-bottom-color: $brown;
			}
		}
	}
	&__submenu {
		font-size: 16px;
		line-height: 1.4;
		margin-top: 40px;
		@include media-breakpoint-up(lg) {
			flex: 1;
			font-size: 14px;
			margin-top: 0;
		}
		@include media-breakpoint-up(xl) {
			font-size: 16px;

		}
		&-list {
			margin: 0;
			padding: 0;
			list-style: none;
		}
		&-itm {
			margin-top: 3px;
		}


		&-lnk{
			color: #FFF;
			padding-bottom: 0;
		}
	}
	&__line {
		display: none;
		align-items: center;
		height: 100%;
		width: 100px;
		position: absolute;
		left: 0;
		top: 0;
		background-color: inherit;
		color: #000;
		z-index: 1;
		@include media-breakpoint-up(lg) {
			display: flex;
			justify-content: center;
		}
		&:after {
			content: '';
			width: 1px;
			position: absolute;
			top: 121px;
			right: 0;
			height: calc(100% - 200px);
			background-color: rgba(#FFF, 0.2);
		}
	}
}
.is-open-menu {
	body {
		position: fixed;
		width: 100%;
	}
}
