.s-albums {
	@include media-breakpoint-up(lg) {
		padding-top: 80px;
	}
	&__list {
		@include media-breakpoint-up(md) {
			display: flex;
			flex-flow: row wrap;
			gap: 30px;
		}
	}
	&__itm {
		margin-top: 10px;
		display: flex;
		@include media-breakpoint-up(md) {
			flex: 0 0 calc(50% - 15px);
			max-width: calc(50% - 15px);
			margin-top: 0;
		}
	}
	&__album {
		overflow: hidden;
		width: 100%;
		background-color: $gray2;
		display: block;
		position: relative;
		min-height: 320px;
		padding: 20px 20px 272px;
		font-size: 20px;
		color: $brown;
		border: none;
		@include media-breakpoint-up(md) {
			min-height: 350px;
			padding: 40px 41px 282px;
		}
	}
	&__ttl {
		font-weight: bold;
	}
	&__album:hover 	&__icon-wrap{
		margin-left: 60px;
	}
	&__icon-wrap {
		position: absolute;
		bottom: 192px;
		left: 20px;
		width: 50px;
		height: 50px;
		z-index: 10;
		background-color: $turquoise2;
		color: #FFF;
		font-size: 14px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 100%;
		transition: margin $time-animation-base;
		@include media-breakpoint-up(md) {
		  left: 41px;
			bottom: 40px;
		}
	}
	&__picture {
		position: absolute;
		right: -44px;
		bottom: -69px;
		width: 261px;
		height: 261px;
		overflow: hidden;
		border-radius: 100%;
		@include media-breakpoint-up(md) {
			width: 384px;
			height: 384px;
			right: -65px;
			bottom: -102px;
		}
	}
	&__album:hover &__img {
		transform: scale(1.3);
	}
	&__img {
		border-radius: 100%;
		display: block;
		width: 100%;
		height: 100%;
		transition: transform $time-animation-base;
	}
}
