.s-suggestion {
	&__desc {
		max-width: 549px;
		@include text-style-large;
	}
	&__list {
		margin-top: 30px;
		@include media-breakpoint-up(md) {
			margin-left: -14px;
			margin-right: -14px;
			display: flex;
			flex-flow: row wrap;
		}
		&_mt-0 {
			margin-top: 0;
		}
	}
	&__itm {
		margin-top: 10px;
		@include media-breakpoint-up(md) {
			padding-right: 14px;
			padding-left: 14px;
			display: flex;
			margin-top: 0;
			flex: 0 0 percentage(1/3);
			max-width: percentage(1/3);
		}

		&_1-4 {
			@include media-breakpoint-up(md) {
				flex: 0 0 percentage(1/2);
				max-width: percentage(1/2);
			}
			@include media-breakpoint-up(xl) {
				flex: 0 0 percentage(1/4);
				max-width: percentage(1/4);
			}
			&-mt-30 {
				@include media-breakpoint-down(lg) {
					@include media-breakpoint-up(md) {
						margin-top: 30px;
					}
				}
			}
		}
	}
	&__box {
		width: 100%;
		border: none;
		background-color: $gray2;
		color: $brown;
		transition: background-color $time-animation-base;
		padding: 20px;
		min-height: 155px;
		display: flex;
		flex-direction: column;
		&:hover {
			background-color: darken($gray2,10%);
		}
		@include media-breakpoint-up(md) {
			padding: 40px;
			min-height: 260px;
		}
	}
	&__box:hover &__icon-wrap {
		left: calc(100% - 50px);
	}
	&__icon-wrap {
		position: relative;
		margin-top: auto;
		left: 0;
		transition: left $time-animation-base*2;
		width: 50px;
		height: 50px;
		border-radius: 100%;
		background-color: $turquoise2;
		color: #FFF;
		font-size: 14px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__ttl {
		margin-bottom: 20px;
		font-size: 18px;
		font-weight: bold;
		@include media-breakpoint-up(lg) {
			font-size: 20px;
		}
	}
}
