.b-layouts {
	margin-top: 40px;
	margin-left: -5px;
	margin-right: -5px;
	display: flex;
	overflow-y: hidden;
	overflow-x: auto;
	-webkit-overflow-scrolling: touch;
	@include media-breakpoint-up(md) {
		margin-top: 50px;
		margin-left: percentage(-15/1274);
		margin-right: percentage(-15/1274);
		overflow: hidden;
		display: block;
	}
	&__itm {
		flex: 0 0 260px;
		max-width: 260px;
		padding-left: 5px;
		padding-right: 5px;
		@include media-breakpoint-up(md) {
			float: left;
			max-width: none;
			padding-left: percentage(15/1274);
			padding-right: percentage(15/1274);
			width: percentage(290/1274);
		}
		&_big {
			@include media-breakpoint-up(md) {
				width: percentage(685/1274);
			}
		}
		&_mb {
			@include media-breakpoint-up(md) {
				padding-bottom: (10/1440*100vw);
			}
			@include media-breakpoint-up(lg) {
				padding-bottom: (25/1440*100vw);
			}
			@include media-breakpoint-up(xxl) {
				padding-bottom: (40/1440*100vw);
			}
		}
	}
}
