@use "sass:math";

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;
}

.icon-angle {
    width: (18 / 10) * 1em;
}
.icon-arrow-right {
    width: (20 / 14) * 1em;
}
.icon-basket {
    width: (24 / 23) * 1em;
}
.icon-car {
    width: (31 / 21) * 1em;
}
.icon-checked {
    width: (37 / 26) * 1em;
}
.icon-close {
    width: (26 / 25) * 1em;
}
.icon-download {
    width: (50 / 70) * 1em;
}
.icon-education {
    width: (24 / 25) * 1em;
}
.icon-leisure {
    width: (24 / 24) * 1em;
}
.icon-medicine {
    width: (23 / 24) * 1em;
}
.icon-phone {
    width: (27 / 25) * 1em;
}
.icon-play {
    width: (15 / 18) * 1em;
}
.icon-telegram {
    width: (24 / 20) * 1em;
}
.icon-watsapp {
    width: (22 / 22) * 1em;
}
