.s-footer {
	flex: 0 0 auto;
	padding: 40px 20px;
	background-color: $turquoise2;
	color: #FFF;
	margin-top: 80px;
	position: relative;
	font-size: 14px;
	line-height: 1.4;
	@include media-breakpoint-up(lg) {
		margin-top: 190px;
		padding: 80px (53/1440*100vw) 72px (40/1440*100vw);
	}
	&:before {
		content: '';
		height: 50px;
		width: 200px;
		left: 0;
		position: absolute;
		bottom: 100%;
		background-color: inherit;
		margin-bottom: -10px;
		@include media-breakpoint-up(lg) {
			width: (473/1440*100vw);
			height: 110px;
		}
	}
	&__top {
		@include media-breakpoint-up(md) {
			display: flex;
		}
	}
	&__logo {
		&-img {
			display: block;
			width: 280px;
			height: 32px;
			@include media-breakpoint-up(md) {
				width: (379/1440*100vw);
				height: (66/1440*100vw);
			}
			@include media-breakpoint-up(lg) {
				width: (579/1440*100vw);
				height: (66/1440*100vw);
			}
		}
	}
	&__submenu {
		font-size: 16px;
		line-height: 1.4;
		margin-top: 50px;
		@include media-breakpoint-up(md) {
			margin-top: 0;

		}
	}
	&__list {
		list-style: none;
		margin: 0;
		padding: 0;
	}
	&__lnk {
		padding-bottom: 0;
		color: inherit;
		display: inline-block;
	}
	&__phone {
		font-size: 24px;
		line-height: 1.55;
		font-weight: bold;
		color: inherit;
		border: none;
		padding-bottom: 0;
		margin-top: 30px;
		display: inline-block;
		@include media-breakpoint-up(md) {
			margin-left: auto;
			margin-top: 0;
		}
	}
	&__btm {
		padding-top: 50px;
		@include media-breakpoint-up(md) {
			padding-top: 80px;
			display: flex;
			align-items: flex-end;
		}
	}
	&__logo-wrap,
	&__copy {
		@include media-breakpoint-up(md) {
			padding-right: 20px;
			max-width: (495/1440*100vw);
			flex: 0 0 (495/1440*100vw);
		}
		@include media-breakpoint-up(lg) {
			padding-right: 20px;
			max-width: (695/1440*100vw);
			flex: 0 0 (695/1440*100vw);
		}
	}
	&__address {
		margin-top: 40px;
		@include media-breakpoint-up(lg) {
			margin-top: 0;
		}
	}
	&__dev {
		margin-top: 40px;
		@include media-breakpoint-up(md) {
			margin-top: 0;
			margin-left: auto;
			text-align: right;
		}
		&-lnk {
			border-bottom-color: transparent;
			color: inherit;
			&:hover {
				border-bottom-color: currentColor;
			}
		}
	}
}
