.s-picts-desc {
	&__list {
		overflow: hidden;
	}
	&__itm {
		padding-bottom: 40px;
		@include media-breakpoint-up(md) {
			float: left;
			width: percentage(549/1244);
		}
		@include media-breakpoint-up(lg) {
			padding-bottom: 90px;
		}
		&_right {
			@include media-breakpoint-up(md) {
				float: right;
			}
		}
		&_pb-0 {
			padding-bottom: 0;
		}
	}
	&__desc {
		margin-top: 20px;
		@include media-breakpoint-up(lg) {
			margin-top: 40px;
		}
		&_mt-0 {
			margin-top: 0;
		}
	}
	&__ttl {
		color: $brown;
		margin: 0;
	}
	&__txt {
		margin-top: 20px;
		@include media-breakpoint-up(lg) {
			margin-top: 40px;
		}
		*:last-child {
			margin-bottom: 0;
		}
	}
	&__img {
		display: block;
		width: 100%;
	}
}
