@import "../../../node_modules/swiper/swiper.scss";
.swiper-button-prev,
.swiper-button-next {
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 100%;
	background-color: $turquoise2;
	color: #FFF;
	width: 30px;
	height: 30px;
	font-size: 9px;
	z-index: 10;
	cursor: pointer;
	transition: background-color $time-animation-base;
	@include media-breakpoint-up(lg) {
		width: 50px;
		height: 50px;
		font-size: 14px;
	}
	&:hover {
		background-color: $brown;
	}
}
.swiper-button-prev {
	&__icon {}
	transform: rotate(180deg);
}
.swiper-pagination {
	display: flex;
	justify-content: flex-end;
	&-bullet {
		width: 7px;
		flex: 0 0 7px;
		margin: 0 5px;
		height: 7px;
		background-color: $brown;
		border-radius: 100px;
		cursor: pointer;
		transition: background-color $time-animation-base;
		&:hover {
			background-color: darken($brown, 10%);
		}
		&-active {
			background-color: #FFF;
		}
	}
}
