.s-maps {
	&__indicators {
		@include media-breakpoint-up(md) {
			display: flex;
			flex-flow: row wrap;
		}
	}

	&__itm {
		margin-top: 20px;

		&:first-child {
			margin-top: 0;
		}

		@include media-breakpoint-up(md) {
			flex: 0 0 50%;
			max-width: 50%;
			margin-top: 0px;
		}
		@include media-breakpoint-up(xl) {
			flex: 0 0 25%;
			max-width: 25%;
		}

		&_mt {
			@include media-breakpoint-down(lg) {
				@include media-breakpoint-up(md) {
					margin-top: 20px;
				}
			}
		}
	}

	&__txt {
		margin-top: 10px;
		max-width: 240px;
	}

	&__icon {
	}
	&__btn-wrap {
		margin-top: 40px;
		@include media-breakpoint-up(lg) {
			margin-top: 50px;
		}
	}
}
