.b-callback {
	background-color: #FFF;
	position: relative;
	color: #000;
	&_mt {
		margin-top: 20px;
		@include media-breakpoint-up(md) {
			margin-top: 40px;
		}
	}
	&__row {
		@include media-breakpoint-up(md) {
			display: flex;
			justify-content: space-between;
		}
	}
	&__col {
		&_first {
			@include media-breakpoint-up(md) {
				max-width: percentage(531/1244);
				flex: 0 0 percentage(531/1244);
			}
		}
		&_two {
			margin-top: 20px;
			@include media-breakpoint-up(md) {
				margin-top: 0;
				max-width: percentage(553/1244);
				flex: 0 0 percentage(553/1244);
			}
			@include media-breakpoint-up(lg) {
				margin-right: (120/1440*100vw);
			}
		}
	}
	&__desc {
		margin-top: 20px;
		@include media-breakpoint-up(lg) {
			margin-top: 40px;
		}
	}
	&_popup &__col {
		&_first {
			@include media-breakpoint-up(md) {
				margin: 0;
			    max-width: percentage(531/1038);
			    flex: 0 0 percentage(531/1038);
			}
		}
		&_two {
			@include media-breakpoint-up(md) {
				margin: 0;
			    max-width: percentage(433/1038);
			    flex: 0 0 percentage(433/1038);
			}

		}
	}
	&_popup {
		display: none;
		max-width: 1188px;
		padding: 25px 15px;
		@include media-breakpoint-up(md) {
			padding: 100px 75px;
		}
	}
	 &.fancybox__content > button.f-button.is-close-btn{
		 display: none;
	}
	&__close {
		top: 0;
		right: 0;
		border-radius: 0;
		width: 50px;
		height: 50px;
		background-color: transparent;
		padding: 0;
		border: none;
		color: $brown;
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		opacity: 1;
		svg {
			width: 25px;
			height: 25px;
		}
	}

}
