.b-section-head {
	margin-bottom: 30px;
	@include media-breakpoint-up(md) {
		margin-bottom: 40px;
	}
	&__ttl {
		margin: 0 0 20px;
		@include text-style-h1;
		@include media-breakpoint-up(md) {
			margin-bottom: 30px;
		}
	}
	&__lnk {
		font-weight: bold;
		font-size: 18px;
		@include media-breakpoint-up(md) {
		}

		&-icon {
			margin-left: 15px;
			font-size: 14px;
			color: $brown;
		}
	}
}
