.s-info-choice {
	@include media-breakpoint-up(lg) {
		padding-top: 100px;

	}
	&__row {
		@include media-breakpoint-up(md) {
			display: flex;
		}
	}
	&__col {
		&_first {
			@include media-breakpoint-up(md) {
				flex: 1;
				padding-right: (164/1440*100vw);
			}
		}
		&_two {
			margin-top: 50px;
			@include media-breakpoint-up(md) {
				max-width: percentage(433/1244);
				margin-top: 0;
			}
		}
	}
	&__btn-wrap {
		&_mt {
			margin-top: 20px;
		}
	}
	&__img {
		width: 100%;
		display: block;
		margin-top: 20px;
	}
}
