.b-info {
	padding: 30px 20px;
	border: 1px solid;
	@include media-breakpoint-up(lg) {
		padding: 60px 86px;
	}
	&__row {
		@include media-breakpoint-up(lg) {
			display: flex;
		}
	}
	&__col {
		&_first {
			flex: 1;
		}
		&_two {
			@include media-breakpoint-up(lg) {
				max-width: ((433+30)*1px);
				flex: 0 0 percentage((433+30)/1070);
				padding-left: 30px;
			}
			@include media-breakpoint-up(xl) {
				max-width: ((433+86+30)*1px);
				flex: 0 0 percentage((433+86+30)/1070);
				padding-left: 30px;
				padding-right: 86px;
			}
		}
	}
	&__ttl {
		font-weight: normal;
	}
	&__right-btn  {
		margin-top: 30px;
		margin-bottom: 30px;
		width: 100%;
		min-width: 0;
		@include media-breakpoint-up(lg) {
			margin: 35px 0;
		}
	}
	&__right-lnk {
		display: inline-block;
		font-weight: bold;
		margin-right: 25px;
	}
	&__prices {
		@include media-breakpoint-up(lg) {
			display: flex;
			margin: 0 -15px;
		}
		&-col {
			margin-top: 30px;
			@include media-breakpoint-up(lg) {
				padding: 0 15px;
				margin-top: 40px;
			}
		}
		&-ttl {
			margin: 0;
			color: $brown;
		}
		ol {
			margin: 10px 0 0 ;
			padding: 0 0 0 20px;
		}
	}
}
